import React,{useContext} from 'react';
import { ThemeContext } from '../context/ThemeContext';
import styles from './Modal.module.css'
import ReactDOM from 'react-dom';

const Modal = (props)=>{
    const [dark_mode] = useContext(ThemeContext);
    const modal_container_class = dark_mode?styles.container:styles.container_light;

    return(
        <div className={modal_container_class}>
            {ReactDOM.createPortal(<div className={styles.backdrop}></div>,document.getElementById("backdrop-root"))}
            <div className={styles.header}>
                <h2 className={styles.header_h2}>{props.msg}</h2>
            </div>
            <div className={styles.body}>
                <p>{props.body}</p>
            </div>
            <footer className={styles.footer}>
                <button className={styles.cancel_btn} onClick={props.onCancel}>Cancel</button>
            </footer>
        </div>
    )
}

export default Modal;