import React, { useContext } from "react";
import { ThemeContext } from "../context/ThemeContext";
import styles from "./NewsCard.module.css";

function ButtonGrp(props) {

const clickHandler = openInTab(props.info.url);
  return (
    <div className={styles.button_grp_container}>
      <div className={styles.button_know_more} onClick={clickHandler}>Know More</div>
      <div className={styles.button_collection}>Add To Collection</div>
      {/* <div className={styles.button_report}>Report</div> */}
      {/* {console.log(props.info.url)} */}
    </div>
  );
}

function openInTab(url){
    return ()=>window.open(url,'_blank');
}

export default function NewsCard(props) {
  //props will have a info attribute which will contain all the news info
  const topic = String(props.info.topic);
  const section = String(props.info.section);
  const source = String(props.info.source.name);
  const headline = source.toUpperCase()+ ' | ' + topic.toUpperCase() + ' | ' + section.toUpperCase() + ' | ' + props.info.title;
  const [dark_mode] = useContext(ThemeContext);
  return (
    <div
      className={`${(dark_mode ? styles.container : styles.container_light)}`}
    >
      {/* <img
        src={props.info.urlToImage}
        alt="Not Found....."
        className={styles.image}
      /> */}
      <div className={styles.inner_container}>
        <div className={styles.title}>{headline}</div>
        <p>{props.info.publishedAt}</p>
        <p>{props.info.description}</p>
        <ButtonGrp info={props.info}/>
      </div>
    </div>
  );
}
