import React, { useContext, useEffect, useCallback, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import styles from "./NewsContainer.module.css";
import newsSlice from "../store/slice/newsSlice";
import userSlice from "../store/slice/userSlice";
import axios from "axios";
import { ThemeContext } from "../context/ThemeContext";
import NewsCard from "./NewsCard";
import Modal from "./Modal";
import {useHistory} from 'react-router-dom';
import Spinner from "./Spinner";

const NewsContainer = () => {
  const [isLoading, setIsLoading] = useState(true);
  const dispatch = useDispatch();
  const [dark_mode] = useContext(ThemeContext);
  const token = useSelector((state) => state.user.token);
    let history = useHistory();

  console.log("rendering all news");

  const getAllNews = useCallback(
    async (page) => {
      const config = {
        headers: { "x-access-token": `Bearer ${token}` },
        withCredentials: false,
      };
      axios
        .get(`https://newssonar.herokuapp.com/news/all`, config)
        .then((res) => {
          dispatch(newsSlice.actions.setAllNews(res.data));
          setIsLoading(false);
        })
        .catch((err) => {
          if (err.response.status === 401) {
            console.log('login required');
            dispatch(userSlice.actions.clearToken());
            history.push('/login');
          }
        });
      // console.log('loaded');
    },
    [dispatch, token,history]
  );
  const newsContainerStyle = dark_mode
    ? styles.news_container
    : styles.news_container_light;
  const newsArr = useSelector((state) => state.news.data.articles);

  const [showModal, setShowModal] = useState(false);

  function onCancel() {
    setShowModal((state) => false);
  }

  useEffect(() => {
    getAllNews(1);
  }, [getAllNews]);

  const list = newsArr.map((newsItem, i) => (
    <NewsCard info={newsItem} key={i} />
  ));

  return (
    <div className={newsContainerStyle}>
      {showModal && (
        <Modal
          msg="This is Header"
          body="This is modal body"
          onCancel={onCancel}
        />
      )}
      {isLoading ? <Spinner/>:list}
      {/* <center><button>Load More</button></center> */}
    </div>
  );
};

export default NewsContainer;
