import React,{useContext} from 'react';
import styles from './Footer.module.css'
import { ThemeContext } from '../context/ThemeContext';

const Footer = ()=>{
    //getting the darkmode state from Theme context
    const [dark_mode] = useContext(ThemeContext);
    //if dark mode is enabled then we are applying footer class else we are applying footer_light 
    const footer_class = dark_mode?styles.footer:styles.footer_light;
    return(
        <>
        <div className={footer_class}>
            <div className = {styles.container}>
                {/* <div>About Us</div> */}
                <div>Designed and developped by Tamal Das</div>
                {/* <div>Copyright</div> */}
            </div>
        </div>
        </>
    )
}

export default Footer;