import React, { useContext, useState } from "react";
import styles from "./SignInForm.module.css";
import img_form from "../form_svg.svg";
import { ThemeContext } from "../context/ThemeContext";
import useInput from "../hooks/input";
import axios from "axios";
import userSlice from "../store/slice/userSlice";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import Spinner from "./Spinner";

const config = {
  withCredentials: false,
};

function LogInFrom() {
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const dispatch = useDispatch();
  const [error, setError] = useState("");

  const [username, isUsernameWrong, usernameChangeHandler, setUsername] =
    useInput("", (val) => val !== "");

  const [password, isPasswordWrong, passwordChangeHandler, setPassword] =
    useInput("", (val) => val.length > 6);

  let isError = isUsernameWrong || !username || !password;

  const submitHandler = (e) => {
    setIsLoading(true);
    const payload = {
      user: {
        username: username,
        password: password,
      },
    };
    // console.log(payload);
    if (isError) {
      setError("Some Field Has Invalid Input");
    } else {
      axios
        .post("https://newssonar.herokuapp.com/login", payload, config)
        .then((res) => {
          if (res.status === 200) {
            setIsLoading(false);
            dispatch(userSlice.actions.setToken(res.data.token));
            setUsername("");
            setPassword("");
            history.push("/allNews");
          }
        })
        .catch((err) => {
          setIsLoading(false);
          console.log(err);
          setError(err.response.data);
        });
    }
  };

  const signInHandler = () => {
    history.push("/signin");
  };

  const [dark_mode] = useContext(ThemeContext);
  return (
    <div className={`${dark_mode ? styles.container : styles.container_light}`}>
      <h2>Please Login</h2>
      {isLoading ? <div className={styles.img}><Spinner/></div> :<img src={img_form} alt="" className={styles.img} />}
      <form action="" className={styles.form}>
        <label
          htmlFor="username"
          className={`${dark_mode ? styles.label : styles.label_light}`}
        >
          User Name
          {isUsernameWrong && (
            <div className={styles.err_banner}>Username cannot be blank</div>
          )}
        </label>
        <input
          type="text"
          id="username"
          value={username}
          onChange={usernameChangeHandler}
          className={`${styles.field}  ${isUsernameWrong ? styles.err : ""}`}
          required
        />

        <label
          htmlFor="password"
          className={`${dark_mode ? styles.label : styles.label_light}`}
        >
          Password
          {isPasswordWrong && (
            <div className={styles.err_banner}>
              Password should at least 7 char long
            </div>
          )}
        </label>
        <input
          type="password"
          id="password"
          className={`${styles.field}  ${isPasswordWrong ? styles.err : ""}`}
          onChange={passwordChangeHandler}
          value={password}
          required
        />

        <br />
        {error != null && <div className={styles.form_err}>{error}</div>}
      </form>
      <button type="submit" className={styles.button} onClick={submitHandler}>
        SUBMIT
      </button>
      <div className={styles.button} onClick={signInHandler}>I don't have account</div>
      <div className={styles.button}>Forgot Password</div>
    </div>
  );
}

export default LogInFrom;
