import React, { useContext, useState } from "react";
import styles from "./SignInForm.module.css";
import img_form from "../form_svg.svg";
import { ThemeContext } from "../context/ThemeContext";
import useInput from "../hooks/input";
import axios from "axios";
import {useHistory} from 'react-router-dom';
import Spinner from "./Spinner";

function SignInFrom() {
  const history = useHistory();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");
  // const [userCreated, setUserCreated] = useState(false);
  const [username, isUsernameWrong, usernameChangeHandler, setUsername] =
    useInput("", (val) => val !== "");
  const [email, isEmailWrong, emailChangeHandler, setEmail] = useInput(
    "",
    (val) => val !== "" && /\S+@\S+\.\S+/.test(val)
  );
  const [cellno, isCellnoWrong, cellnoChangeHandler, setCellNo] = useInput(
    "",
    (val) => val !== "" && RegExp("[0-9]{10}").test(val)
  );
  const [password, isPasswordWrong, passwordChangeHandler, setPassword] =
    useInput("", (val) => val.length > 6);
  const [password2, isPassword2Wrong, passwordChangeHandler2, setPassWord2] =
    useInput("", (val) => val.length > 6);

  let isError =
    isUsernameWrong ||
    isCellnoWrong ||
    isEmailWrong ||
    isPasswordWrong ||
    isPasswordWrong ||
    !username ||
    !email ||
    !cellno ||
    !password ||
    !password2;

  const LogInHandler = () => {
    history.push("/login");
  };
  const submitHandler = (e) => {
    setIsLoading(true);
    const payload = {
      user: {
        username: username,
        email: email,
        cellno: cellno,
        password: password,
        password2: password2,
      },
    };
    // console.log(payload);
    if (isError) {
      setError("Some Field Has Invalid Input");
      setIsLoading(false);
    } else if (payload.user.password === payload.user.password2) {
      axios
        .post("https://newssonar.herokuapp.com/register", payload)
        .then((res) => {
          if (res.status === 200) {
            setIsLoading(false);
            setUsername("");
            setCellNo("");
            setEmail("");
            setPassword("");
            setPassWord2("");
            setError("");
            history.push("/login");
          }
        })
        .catch((err) =>{
          setError("Account already exists with this email and cell no");
          setIsLoading(false);
        });
    } else {
      console.log(
        "password not matching with confirm password or any other error"
      );
      setError("pasword and confirmed password are different");
      isError = true;
    }
  };

  const [dark_mode] = useContext(ThemeContext);
  return (
    <div className={`${dark_mode ? styles.container : styles.container_light}`}>
      <h2>Please Sign In</h2>
      {isLoading ? <div className={styles.img}><Spinner/></div> :<img src={img_form} alt="" className={styles.img} />}
      
      <form action="" className={styles.form}>
        <label
          htmlFor="username"
          className={`${dark_mode ? styles.label : styles.label_light}`}
        >
          User Name
          {isUsernameWrong && (
            <div className={styles.err_banner}>Username cannot be blank</div>
          )}
        </label>
        <input
          type="text"
          id="username"
          value={username}
          onChange={usernameChangeHandler}
          className={`${styles.field}  ${isUsernameWrong ? styles.err : ""}`}
          required
        />

        <label
          htmlFor="email"
          className={`${dark_mode ? styles.label : styles.label_light}`}
        >
          Email
          {isEmailWrong && (
            <div className={styles.err_banner}>Not a valid email</div>
          )}
        </label>
        <input
          type="text"
          id="email"
          value={email}
          onChange={emailChangeHandler}
          className={`${styles.field}  ${isEmailWrong ? styles.err : ""}`}
          required
        />
        <label
          htmlFor="cell"
          className={`${dark_mode ? styles.label : styles.label_light}`}
        >
          Mobile No
          {isCellnoWrong && (
            <div className={styles.err_banner}>
              Mobile number should have 10 digits
            </div>
          )}
        </label>
        <input
          type="text"
          id="cell"
          value={cellno}
          onChange={cellnoChangeHandler}
          className={`${styles.field}  ${isCellnoWrong ? styles.err : ""}`}
          required
        />
        <label
          htmlFor="password"
          className={`${dark_mode ? styles.label : styles.label_light}`}
        >
          Password
          {isPasswordWrong && (
            <div className={styles.err_banner}>
              Password should at least 7 char long
            </div>
          )}
        </label>
        <input
          type="password"
          id="password"
          className={`${styles.field}  ${isPasswordWrong ? styles.err : ""}`}
          onChange={passwordChangeHandler}
          value={password}
          required
        />
        <label
          htmlFor="confirm_password"
          className={`${dark_mode ? styles.label : styles.label_light}`}
        >
          Confirm Password
          {isPassword2Wrong && (
            <div className={styles.err_banner}>
              Password should at least 7 char long
            </div>
          )}
        </label>
        <input
          type="password"
          id="confirm_password"
          className={`${styles.field}  ${isPassword2Wrong ? styles.err : ""}`}
          onChange={passwordChangeHandler2}
          value={password2}
          required
        />
        <br />
        {error != null && <div className={styles.form_err}>{error}</div>}
      </form>
      <button type="submit" className={styles.button} onClick={submitHandler}>
        Sign In
      </button>
      <div className={styles.button} onClick={LogInHandler}>Already have account</div>
    </div>
  );
}

export default SignInFrom;
