import { useState } from "react";

const useInput = (initialValue, validator) => {
  //initially is valid and is changed is false
  const [isValid, setIsValid] = useState(false);
  const [isChanged, setIsChanged] = useState(false);
  const [value, setValue] = useState(initialValue);

  //if the input field is changed and still the input is not valid then it is wrong we need to show some error
  //but the error will  not be shown at the starting as user has not tried to make any change
  const isWrong = !isValid && isChanged; //initially isChangeed id false so isWrong is false so error is not shown

  const valueChangeHandler = (event) => {
    if (!isChanged) {
      setIsChanged(true);
    }
    // validator would return true if the input is valid else return false
    if (validator(event.target.value)) {
    //   console.log("valid input");
      setIsValid(true);
    }
    else{
        setIsValid(false);
    }
    setValue(event.target.value);
    // console.log({ isChanged, isValid, isWrong });
  };

  //we need to expose the
  //value to bind with the input field
  //isWrong to show error
  //valueChangehandler so that we can make state change by passing the event
  return [value, isWrong, valueChangeHandler,setValue];
};

//exporting the useInput hook
export default useInput;
