import React from "react";
import Navbar from "./component/Navbar.jsx";
import { ThemeProvider } from "./context/ThemeContext";
import store from "./store/store";
import { Provider } from "react-redux";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import SignInFrom from "./component/SignInForm.jsx";
import NewsContainer from "./component/NewsContainer.jsx";
import LogInFrom from "./component/LoginFrom.jsx";
// import GoogleChart from "./component/GoogleChart.jsx";
import Footer from "./component/Footer";
import "./App.css";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Provider store={store}>
          <ThemeProvider>
            <div className="nav-container">
              <Navbar />
            </div>
            <div className="placeholder-container"></div>
            <div className="content-container">
              <Switch>
                <Route exact path="/">
                  <NewsContainer />
                </Route>
                <Route exact path="/allNews">
                  <NewsContainer />
                </Route>
                <Route exact path="/myCollection">
                  <div>This is my collection page</div>
                </Route>
                <Route exact path="/profile">
                  <div>This is profile page</div>
                </Route>
                <Route exact path="/signin">
                  <SignInFrom />
                </Route>
                <Route exact path="/login">
                  <LogInFrom />
                </Route>
              </Switch>
            </div>
            <div className="footer-container">
              <Footer />
            </div>
          </ThemeProvider>
        </Provider>
      </BrowserRouter>
    </div>
  );
}

export default App;
