import { useContext } from "react";
import React from "react";
import styles from "./Navbar.module.css";
import { ThemeContext } from "../context/ThemeContext";
import { Link } from "react-router-dom";
import { useSelector,useDispatch } from "react-redux";
import userSlice from "../store/slice/userSlice";

// const style = {color:'red'}

function Navbar() {
  const dispatch = useDispatch();
  const [dark_mode, setDarkMode] = useContext(ThemeContext);
  const user = useSelector((store) => store.user);
  let mode = dark_mode ? "LIGHT" : "DARK";

  const logoutHandler = ()=>{
    dispatch(userSlice.actions.clearToken());
  }

  const modeToggleHandler = () => {
    setDarkMode((prev_state) => !prev_state);
  };
  let nav_element_style = dark_mode
    ? styles.nav_element
    : styles.nav_element_light;
  return (
    <div
      className={`${
        dark_mode ? styles.head_container : styles.head_container_light
      }`}
    >
      <div className={styles.brand}>News Sonar</div>
      {/* <div className="logo"></div>  */}
      <div className={styles.nav_links}>
        <Link to="/allNews" style={{ "textDecoration": "none" }}>
          <div className={nav_element_style}>All News</div>
        </Link>
        <Link to="/myCollection" style={{ "textDecoration": "none" }}>
          <div className={nav_element_style}>My Collection</div>
        </Link>
        <Link to="/login" style={{ "textDecoration": "none" }}>
          {/* <div className={nav_element_style}>{`${user.isLoggedIn ? "Logout":"Login"}`}</div> */}
          {!user.isLoggedIn ? <div className={nav_element_style}>Login</div>:null}
        </Link>
        {user.isLoggedIn ? <div className={nav_element_style} onClick={logoutHandler}>logout</div>:null}
        <Link to="/profile">
          <img src={user.avatar} alt="Avatar" className={styles.user_avatar} />
        </Link>
        <div
          className={`${styles.button} ${dark_mode ? "" : styles.button_light}`}
          onClick={modeToggleHandler}
        >
          <div className={`${dark_mode ? styles.label : styles.label_light}`}>
            {mode}
          </div>
          <div
            className={`${dark_mode ? styles.knob : styles.knob_light}`}
          ></div>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
